
body {
    font: 14px/1.6 Arial,"Microsoft YaHei","\9ED1\4F53","\5B8B\4F53",sans-serif;
    color: #333;
    margin: 0;
}
html, body, #app {
    height: 100%;
}
.el-button {
    color: rgb(0, 153, 0);
    border: 1px solid rgba(0, 153, 51, 0.6);
    background: none;
    border-radius: 4px;
}
.el-button.el-button--medium{
    font-size: 13px;
    padding: 0 8px;
    font-weight: normal;
    line-height: 28px;
}
.el-button:hover {
    color: #fff;
    background-color: rgba(0, 153, 51, 0.6);
}
.el-input.is-active .el-input__inner, .el-input__inner:focus, .el-textarea__inner:focus {
    border-color: rgba(0, 153, 51, 0.6);
}
#app {
  height:100%;
  display:grid;
  grid-template-rows:auto 1fr auto;
  grid-template-columns:12% auto 12%;
  grid-template-areas:'header header header'
                      '. main . '
                      'footer footer footer'
}
#header {
  grid-area:header;
  padding:5px 20px;
}
#footer {
  grid-area:footer;
  padding:5px 20px;
}
#main {
  grid-area:main;
  padding:10px 20px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

header {
  background-color:#ccc;
  padding:0;
  display:grid;
  grid-template-columns:12% auto 12%;
}
h1 {
  font-size:32px;
  margin:10px;
  color:rgb(0, 153, 0);
}
h1 a {
  text-decoration:none;
  color:inherit;
}
.no-login {
  grid-template-rows:auto auto;
}
.no-login h1 {
  grid-column:2;
  grid-row:1;
  -ms-flex-item-align:center;
      align-self:center;
}
.no-login .btns {
  grid-column:2;
  grid-row:2;
}
.login h1 {
  grid-column:2/3;
  justify-self:center;
  -ms-flex-item-align:center;
      align-self:center;
  grid-area:'middle';
}
ul {
  padding:0;
  padding-right:20px;
}
ul a {
  text-decoration:none;
  color: rgb(0, 153, 0);
}
header.login {
  grid-template-rows:1fr;
  grid-template-areas:'. middle right'
}
.login .user {
  grid-area:'right';
  grid-column:3/4;
}
.log,
.reg {
  padding:10px;
  margin:0 10px 10px 10px;
}

footer {
    background-color:#ccc;
    font-size:12px;
}

a {
  text-decoration:none;
}
.item {
  display: grid;
  grid: auto auto / 80px 1fr;
  margin: 20px 0;
  text-overflow:hidden;
}
.item .avatar {
  grid-column: 1;
  grid-row: 1 / span 2;
  justify-self: center;
  margin-left: 0;
  text-align: center;
}
.item .avatar img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
}
.item .avatar figcaption {
    font-size: 12px;
    color:  #999;
}
.item h3 {
    grid-column: 2;
    grid-row: 1;
    text-align:left;
    color:#333;
}
.item h3 span {
    color:  #999;
    font-size: 12px;
    font-weight: normal;
}
.item p {
    text-align:left;
    text-indent:2em;
    grid-column: 2;
    grid-row: 2;
    margin-top: 0;
    color:#555;
}
.item a {
    text-decoration:none;
}
.item .ct {
    padding-left:20px;
}









